      "Texas (/\u02c8t\u025bks\u0259s/\\\\, locally /\u02c8t\u025bks\u026az/;[9] Spanish: Texas or Tejas\\\\, pronounced [\u02c8texas] (About this soundlisten)) is the second largest state in the United States by area (after Alaska) and population (after California). Geographically located in the South Central region of the country\\\\, Texas shares borders with the U.S. states of Louisiana to the east\\\\, Arkansas to the northeast\\\\, Oklahoma to the north\\\\, New Mexico to the west\\\\, and the Mexican states of Chihuahua\\\\, Coahuila\\\\, Nuevo Le\u00f3n\\\\, and Tamaulipas to the southwest\\\\, and has a coastline with the Gulf of Mexico to the southeast.",
